import './App.css';
import React from 'react'
import ReactFC from "react-fusioncharts";
import FusionCharts from "fusioncharts";
import Column2D from "fusioncharts/fusioncharts.charts";
import FusionTheme from "fusioncharts/themes/fusioncharts.theme.fusion";
ReactFC.fcRoot(FusionCharts, Column2D, FusionTheme);

class App extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
        }
    }

    render() {
        const chartData = [
            {
                label: "7/11",
                value: "1596"
            },
            {
                label: "8/11",
                value: "1115"
            },
            {
                label: "9/11",
                value: "912"
            },
            {
                label: "10/11",
                value: "832"
            },
            {
                label: "11/11",
                value: "1261"
            },
            {
                label: "12/11",
                value: "1212"
            },
            {
                label: "13/11",
                value: "1357"
            },
            {
                label: "14/11",
                value: "1118"
            }
        ];
        const chartConfigs = {
            type: "column2d", 
            width: "700", 
            height: "400", 
            dataFormat: "json", 
            dataSource: {
                chart: {
                    caption: "Cases per day", 
                    subCaption: "last 7 days",
                    xAxisName: "Date",           
                    yAxisName: "Cases", 
                    numberSuffix: "",
                    theme: "fusion"
                },
                data: chartData
            }
        };

        return (
            <div className="App">
                <ReactFC {...chartConfigs} />
            </div>
        );
    }
}

export default App;
