import './App.css';
import React from 'react'
import ReactFC from 'react-fusioncharts';
import FusionCharts from 'fusioncharts';
import Doughnut2D from 'fusioncharts/fusioncharts.charts';
import FusionTheme from 'fusioncharts/themes/fusioncharts.theme.fusion';
ReactFC.fcRoot(FusionCharts, Doughnut2D, FusionTheme);

class App extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
        }
    }
    render() {
        const data = [{
            "label": "0-4",
            "value": "155"
        }, {
            "label": "5-14",
            "value": "544"
        }, {
            "label": "15-19",
            "value": "558"
        }, {
            "label": "20-24",
            "value": "704"
        }, {
            "label": "25-44",
            "value": "2549"
        }, {
            "label": "45-64",
            "value": "2326"
        }, {
            "label": "65-74",
            "value": "437"
        }, {
            "label": "75-84",
            "value": "301"
        }, {
            "label": "85+",
            "value": "231"
        }]

        const chartConfigs = {
            type: 'doughnut2d',
            renderAt: 'chart-container',
            width: '700',
            height: '450',
            dataFormat: 'json',
            dataSource: {
                "chart": {
                    "caption": "Cases Per Age Group",
                    "subCaption": "Last 7 Days",
                    "bgColor": "#ffffff",
                    "startingAngle": "310",
                    "showLegend": "1",
                    "defaultCenterLabel": "Total Cases: 7807",
                    "centerLabel": "$label: $value",
                    "centerLabelBold": "1",
                    "showTooltip": "0",
                    "theme": "fusion"
                },
                "data": data
            }
        };
        return (
            <div className="App">
                <ReactFC {...chartConfigs} />
            </div>
        );
    }
}

export default App;
