import './App.css';
import React from 'react'
import ReactFC from "react-fusioncharts";
import FusionCharts from "fusioncharts";
import Column2D from "fusioncharts/fusioncharts.charts";
import FusionTheme from "fusioncharts/themes/fusioncharts.theme.fusion";
ReactFC.fcRoot(FusionCharts, Column2D, FusionTheme);

class App extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
        }
    }

    render() {
        const chartData = [
            {
                label: "30/10",
                value: "12"
            },
            {
                label: "31/10",
                value: "7"
            },
            {
                label: "1/11",
                value: "10"
            },
            {
                label: "2/11",
                value: "10"
            },
            {
                label: "3/11",
                value: "11"
            },
            {
                label: "4/11",
                value: "5"
            },
            {
                label: "5/11",
                value: "6"
            },
            {
                label: "6/11",
                value: "7"
            }
        ];
        const chartConfigs = {
            type: "column2d", 
            width: "700", 
            height: "400", 
            dataFormat: "json", 
            dataSource: {
                chart: {
                    caption: "ICU Admissions per day", 
                    subCaption: "last 7 days",
                    xAxisName: "Date",           
                    yAxisName: "Admissions", 
                    numberSuffix: "",
                    theme: "fusion"
                },
                data: chartData
            }
        };

        return (
            <div className="App">
                <ReactFC {...chartConfigs} />
            </div>
        );
    }
}

export default App;
