import './App.css';
import React from 'react'
import ReactFC from 'react-fusioncharts';
import FusionCharts from 'fusioncharts';
import Pareto2D from 'fusioncharts/fusioncharts.charts';
import FusionTheme from 'fusioncharts/themes/fusioncharts.theme.fusion';
ReactFC.fcRoot(FusionCharts, Pareto2D, FusionTheme);

class App extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            today: true,
            yesterday: false,
        }
        this.showToday = this.showToday.bind(this);
        this.showYesterday = this.showYesterday.bind(this)

    }
    showToday() {
        this.setState({
            yesterday: false,
            today: true,
        })
    }
    showYesterday() {
        this.setState({
            yesterday: true,
            today: false,
        })
    }
    render() {
        let chartData;
        if (this.state.today) {
            chartData = [{
                "label": "0-4",
                "value": "155"
            }, {
                "label": "5-14",
                "value": "544"
            }, {
                "label": "15-19",
                "value": "558"
            }, {
                "label": "20-24",
                "value": "704"
            }, {
                "label": "25-44",
                "value": "2549"
            }, {
                "label": "45-64",
                "value": "2326"
            }, {
                "label": "65-74",
                "value": "437"
            }, {
                "label": "75-84",
                "value": "301"
            }, {
                "label": "85+",
                "value": "231"
            }
            ];
        } else {
            chartData = [{
                "label": "0-24",
                "value": "297"
            }, {
                "label": "25-44",
                "value": "385"
            }, {
                "label": "45-64",
                "value": "322"
            }, {
                "label": "65+",
                "value": "138"
            }
            ];
        }


        const chartConfigs = {
            type: 'pareto2d',
            renderAt: 'chart-container',
            width: '700',
            height: '390',
            dataFormat: 'json',
            dataSource: {
                "chart": {
                    caption: "New Cases",
                    subCaption: "Per Age Group",
                    "xAxisName": "Age Group",
                    "pYAxisName": "No. of Positive cases",
                    "sYAxisname": "Cumulative Percentage",
                    "showHoverEffect": "1",
                    "theme": "fusion"
                },
                "data": chartData
            }

        };


        return (
            <div className="App">

                <ReactFC {...chartConfigs} />
                <div class="dropdown">
                    <button class="dropbtn" onClick={this.showToday}>Today</button>
                    <button class="dropbtn" onClick={this.showYesterday}>Yesterday</button>

                </div>

            </div>
        );
    }
}

export default App;
